.sidebar{
    width: 30%;
    overflow-y:scroll;
    overflow-x: hidden
}

.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}

.sidebar_searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}

.sidebar_searchContainer > input{
    border:none;
    margin-left: 10px;
}
.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}

.sidebar_header{
    display: flex;
    /* justify-content: space-between; */
    padding: 20px;
    border-right: 1px solid lightgray;
}
hr{
    margin:0
}

.sidebar_headerRight{
    display: flex;
    align-items: center;    
    padding: 0px 20px;
    font-weight: 700;
    /* justify-content: space-between; */
    /* min-width: 10vw; */
    font-size: 1.5rem;
}

.sidebar_headerRight > .MuiSvgIcon-root {
    margin-right: 2vw;
    font-size: 20px !important;
}